import {GoogleLogin} from 'react-google-login';

const clientId = "526378444815-2900t9b82mdc6eu6troa7i7ih43bga7f.apps.googleusercontent.com";

export function Login(){

    const onSuccess = (res) => {
        console.log("Login Success", res.profileObj);
    }

    const onFailure = (res) => {
        console.log("Login Failed", res);
    }

    return(
        <div id="signinbutton">
        <GoogleLogin
            clientId={clientId}
            buttonText='Login'
            onSuccess={onSuccess}
            onFailure={onFailure}
            cookiePolicy={'single_host_origin'}
            isSignedIn={true}
        />
            </div>
    )
}