import {GoogleLogin} from 'react-google-login';

const clientId = "526378444815-2900t9b82mdc6eu6troa7i7ih43bga7f.apps.googleusercontent.com";

export function Logout(){

    const onSuccess = (res) => {
        console.log("Login Success", res.profileObj);
    }


    return(
        <div id="signoutbutton">
        <GoogleLogin
            clientId={clientId}
            buttonText='Logout'
            onSuccess={onSuccess}
            
        />
            </div>
    )
}