import { useEffect } from 'react';
import {gapi} from 'gapi-script'
import { Login } from './components/Login';
import './App.css';
import { Logout } from './components/Logout';

const clientId = "526378444815-2900t9b82mdc6eu6troa7i7ih43bga7f.apps.googleusercontent.com";

function App() {

  useEffect(()=>{
    function start(){
      gapi.client.init({
        clientId:clientId,
        scope: ""
      })
    };
    gapi.load('client:auth2',start)
  })
  return (
    <div className="App">
      <Login/>
      <Logout/>
    </div>
  );
}

export default App;
